* {
  margin: 0;
  padding: 0;
  font-size : 0;
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  word-break: break-all;
  font-family: 'Heebo', sans-serif;
}

img{
  width:100%;
  height:100%;
}
a{
  text-decoration: none;
  font-size : 14px;
  text-transform:uppercase;
  color:black;
}
ul{
  list-style : none;
}

/* header */
.header-area{
    position:relative;
    top:0;
    left:0;
    width:100%;
    z-index : 99;
    background-color:white;
    box-shadow:0px 1px 10px rgba(0,0,0,0.3);
}
#header{
    animation-name:nav-fade;
    animation-duration:0.5s;
    -webkit-animation-name : nav-fade;
    -webkit-animation-duration:0.5s;
}
@keyframes nav-fade {
    from { opacity: 0.4; }
    to { opacity:1; }
}
@-webkit-keyframes nav-fade{
    from { opacity: 0.4; }
    to { opacity:1; }
}
.header-area.nav-fixed{
    position:fixed;
}
/* header > nav */
.header-area > .nav-area{
    width:calc(100% - 120px);
    margin:0 60px;
    overflow:hidden;
}
@media(min-width:992px){
    .header-area > .nav-area{
        max-width:900px;
        margin:0 auto;
    }
}
@media(min-width:1200px){
    .header-area > .nav-area{
        max-width:1000px;
    }
}
/* nav logo set */
.header-area > .nav-area > .nav-logo{
    display:inline-block;
    position:absolute;
    top:50%;
    -webkit-transform:translateY(-50%);
            transform:translateY(-50%);
    font-size:32px;
    font-family: 'Baloo Chettan', cursive;
}
/* nav toggle set */
.header-area > .nav-area > .nav-toggle{
    float:right;
    height:70px;
    line-height:70px;
    cursor:pointer;
    font-size:32px;
}
.header-area > .nav-area > .nav-toggle i{
    font-size : 32px;
}
@media(min-width:992px){
    .header-area > .nav-area > .nav-toggle{
        display:none;
    }
}
/* nav menu set */
.header-area > .nav-area > .nav-menu{
    position:absolute;
    background-color:rgba(0,0,0,0.5);
    top:70px;
    left:0;
    width:100%;
    height:0px;
    transition:0.5s ease;
    overflow: hidden;
}
.header-area > .nav-area > .nav-menu.show {
    height:250px;
}
/* nav menu item set */
.header-area > .nav-area > .nav-menu >.nav-item{
    float:none;
    display:block;
    height:50px;
    line-height:50px;
}
.header-area > .nav-area > .nav-menu > .nav-item:hover{
    background-color:rgba(0,0,0,0.4);
}
.header-area > .nav-area > .nav-menu > .nav-item > .nav-link{
    display:block;
    padding-left:50px;
    color:white;
    cursor:pointer;
}
.header-area > .nav-area > .nav-menu > .nav-item > .nav-link:hover{
    color:red;
    font-weight: bold;
}
@media(min-width:992px){
    .header-area > .nav-area > .nav-menu {
        position:relative;
        background-color:transparent;
        float:right;
        top:0;
        width:auto;
        height:auto;
        transition:none;
    }
    .header-area > .nav-area > .nav-menu.show{
        height:auto;
    }
    .header-area > .nav-area > .nav-menu > .nav-item{
        display:inline-block;
        height:70px;
        line-height:70px;
    }
    .header-area > .nav-area > .nav-menu > .nav-item:hover{
        background-color:transparent;
    }
    .header-area > .nav-area > .nav-menu > .nav-item > .nav-link{
        display:block;
        padding:0 20px;
        color:black;
    }
}
.Title{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content:center;
            justify-content:center;
    background-image:url(/React-PortFolio/static/media/bg.0e695565.jpg);
    width:100%;
    height:100vh;
    background-attachment:fixed;
    background-size:cover;
    background-repeat:no-repeat;
 }

.Title *{
    color:white;
    font-size:50px;
    font-family: 'Sunflower', sans-serif;
}

.wrap {
    border-right: .05em solid;
    -webkit-animation: caret 1s steps(1) infinite;
            animation: caret 1s steps(1) infinite;
}

@-webkit-keyframes caret {
    50% {
        border-color: transparent;
    }
}

@keyframes caret {
    50% {
        border-color: transparent;
    }
}
/* section */
section{
    padding:50px 0 0 0;
    max-width:1000px;
    margin:0 auto;
}
/* about area */
.about-area{
    height:auto;
    position:relative;
}
/* title */
.title{
    font-size:50px;
    font-weight:bold;
    text-align:center;
    letter-spacing:3px;
    padding-bottom:50px;
}
.title::after{
    content:"";
    display:block;
    width:100px;
    height:10px;
    margin:0 auto;
    background-image:linear-gradient(to right,palevioletred,navajowhite);
}
/* about picture */
.about-area > .picture{
    display:block;
    text-align:center;
    padding:20px;
    margin-bottom:30px;
}
@media(min-width:992px){
    .about-area > .picture{
        width:calc(40% - 40px);
        display:inline-block;
    }
}
.about-area > .picture > img{
    max-width:300px;
    max-height:400px;
    border-radius:15px;
}
.about-area > .picture > .name{
    font-size:25px;
    text-align: center;
    padding:15px 0;
    letter-spacing:5px;
}
.about-area > .picture > .job{
    font-size:14px;
    font-family:'Noto Sans KR',sans-serif;
}
/* about-area text */
.about-area > .text{
    display:block;
    padding:30px;
    position:relative;
    top:-40px;
}
.about-area > .text *{
    font-size:16px;
}
@media(min-width:992px){
    .about-area > .text{
        width:calc(60% - 100px);
        display:inline-block;
        padding:0 50px;
    }
}
.about-area > .text > .intro{
    padding:20px 0;
    font-family:'Noto Sans KR',sans-serif;
    letter-spacing:2px;
}
.about-area > .text > ul{
    padding:20px 0;
}
.about-area > .text > ul > li{
    height:40px;
}
.about-area > .text > ul > li > .info{
    color:#777;
}
.about-area > .text > ul > li > .info > i {
    color:darkgreen; margin-right:3px;
}
/* sns */
.sns {
    text-align:center;
    margin-bottom:30px;
}
.sns > a {
    width:50px;
    line-height: 50px;
    border:1.5px solid rosybrown;
    border-radius:5px;
    display:inline-block;
    text-align:center;
    cursor:pointer;
    margin:0 5px;
}
.sns > a:hover{
    background-image:linear-gradient(to right,navajowhite,palevioletred);
}
.sns > a > i{
    font-size:23px;
    margin-top:18px;
}
/* education */
.education *{
    font-size:16px;
}
#educationTitle{
    font-size:24px;
    font-weight:bold;
    margin-bottom:20px;
    text-align:center;
    font-family: 'Roboto Mono', monospace;
    color:mediumpurple;
;

}
.education_part{
    text-align: center;
    display:block;
    font-family: 'Noto Sans KR', sans-serif;
}
.education_part div:nth-child(3){
    color:#777;
    margin:15px 0;
}
@media(min-width:992px) {
   .education_part{
       display:inline-block;
       margin:0px 20px 20px;
   }#educationTitle{
        text-align:left;
   }
}
/* skills-area */
.skills-area *{
    font-size:24px;
    text-align:center;
}
.skills-area{
    margin:0 auto;
    height:auto;
    background-color:whitesmoke;
    padding:20px 0;
}
/* skills-title */
.skills-title{
    font-size:45px;
    font-weight:bold;
}
.skills-title::after{
    content:"";
    display:block;
    width:100px;
    height:10px;
    margin:0 auto 40px;
    background-image:linear-gradient(to right,palevioletred,navajowhite);
}
/* skills-images */
.skills-images {
    font-weight:600;
}
.skills-images div:nth-child(1){
    margin:10px 0;
}
/* skills-images > skills-image*/
.skills-images > .skills-image {
    width:120px;
    height:120px;
    margin:10px;
    box-shadow:0px 5px 10px rgba(0,0,0,0.3);
    border-radius:30px;
    /*padding:5px;*/
    background-color:white;
    display:inline-block;
    background-size:cover;
    position:relative;
}
.skills-images .skills-image:nth-child(odd){
    -webkit-animation:imgMoveOdd;
            animation:imgMoveOdd;
    -webkit-animation-duration:3s;
            animation-duration:3s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
}
.skills-images .skills-image:nth-child(even){
    -webkit-animation:imgMoveEven;
            animation:imgMoveEven;
    -webkit-animation-duration:3s;
            animation-duration:3s;
    -webkit-animation-iteration-count:infinite;
            animation-iteration-count:infinite;
}
@-webkit-keyframes imgMoveOdd{
    0%{ -webkit-transform:translateY(0); transform:translateY(0)}
    50%{ -webkit-transform:translateY(20px); transform:translateY(20px)}
    100%{ -webkit-transform:translateY(0px); transform:translateY(0px)}
}
@keyframes imgMoveOdd{
    0%{ -webkit-transform:translateY(0); transform:translateY(0)}
    50%{ -webkit-transform:translateY(20px); transform:translateY(20px)}
    100%{ -webkit-transform:translateY(0px); transform:translateY(0px)}
}
@-webkit-keyframes imgMoveEven{
    0%{ -webkit-transform:translateY(20px); transform:translateY(20px)}
    50%{ -webkit-transform:translateY(0); transform:translateY(0)}
    100%{ -webkit-transform:translateY(20px); transform:translateY(20px)}
}
@keyframes imgMoveEven{
    0%{ -webkit-transform:translateY(20px); transform:translateY(20px)}
    50%{ -webkit-transform:translateY(0); transform:translateY(0)}
    100%{ -webkit-transform:translateY(20px); transform:translateY(20px)}
}
.skills-image .visit{
    font-size:15px;
    color:white;
    width:120px;
    height:120px;
    position:absolute;
    line-height:120px;
    border-radius:30px;
    visibility:hidden;
    top:-10px;
    background-color:#000;
    opacity:0;
    transition:opacity .8s ease;
}
.skills-image:hover .visit{
    visibility:visible;
    opacity:.8;
}


/* project-area */
#project{
    margin-top:50px;
}
.project-area{
    height:auto;
    max-width:1000px;
    margin:0 auto;
}
/* project-title */
.project-title{
    font-size:45px;
    font-weight:bold;
    text-align:center;
    letter-spacing:3px;
}
.project-title::after{
    content:"";
    display:block;
    width:100px;
    height:10px;
    margin:0 auto;
    background-image:linear-gradient(to right,palevioletred,navajowhite);
}
/* project-area > filter > list */
.project-area > .filter > .list{
    text-align:center;
}
.project-area > .filter > .list > .list-item{
    display:inline-block;
    font-weight:bold;
    padding:0 20px;
    font-size:16px;
    cursor:pointer;
}
.project-area > .filter > .list > .list-item:hover,
.project-area > .filter > .list > .list-item.active{
    color:royalblue;
}
/* project-area > project-container */
.project-area > .project-container{
    padding-top:50px;
}
/* project-area > project-container > filter-item */
.project-area > .project-container > .filter-item{
    display:inline-block;
    width:calc(100% - 40px);
    margin:20px;
    text-align:center;
}
@media(min-width:992px){
    .project-area > .project-container > .filter-item{
        width:calc(50% - 40px);
    }
}
.project-area > .project-container > .filter-item.show {
    display:inline-block;
}
.project-area > .project-container > .filter-item > .image{
    position:relative;
    width:100%;
    max-height:300px;
}
@media(min-width:992px){
    .project-area > .project-container > .filter-item > .image{
        height:300px;
        width:100%;
    }
}
.project-area > .project-container > .filter-item > .image > .overlay{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    opacity:0;
    transition: all 0.3s ease;
    cursor : pointer;


}
.project-area > .project-container > .filter-item > .image:hover > .overlay{
    background-image:linear-gradient(to right,navajowhite,palevioletred);
    opacity:0.9;
}
.project-area > .project-container > .filter-item > .image:hover > .overlay > i{
    position:absolute;
    top:50%;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
    color:white;
    font-size:48px;
}
.project-area > .project-container > .filter-item > .image > img{
    border-radius: 5px;
    height:300px;
    width:100%;
}
.project-area > .project-container > .filter-item > .main{
    font-size:24px;
    font-weight:bold;
    padding:10px 0;
}
.project-area > .project-container > .filter-item > .sub {
    font-size:16px;
    color:#777;
}
.project-area > .project-container > .filter-item > .text{
    display:none;
    color:white;
}
.pdf-text{
    color:#bbb;
    font-size:16px;
}
.pdf-text:hover{
    color:rosybrown;
}
/* portfolioModal */
#portfolioModal{
    display:none;
    position:fixed;
    z-index : 100;
    left:0;
    top:0;
    width:100%;
    height:100%;
    overflow:auto;
    background-color:rgba(0,0,0,0.9);
    text-align:center;
    overflow: hidden;
}
/* portfolioModal > close */
#portfolioModal > .close {
    display:block;
    text-align:right;
    color:#f1f1f1;
    font-size:40px;
    font-weight:bold;
    transition:0.3s;
    cursor:pointer;
    padding:0 20px;
}
#portfolioModal > .close:hover,
#portfolioModal > .close:focus{
    color:#bbb;
}
/* portfolioModal > modal-container */
#portfolioModal > .modal-container{
    width:100%;
    height:auto;
}
#portfolioModal > .modal-container > img{
    width:calc(60% - 40px);
    padding:20px;
    border-radius:30px;
    height:300px;
}
@media(min-width:992px){
    #portfolioModal > .modal-container > img {
        max-width:500px;
        height:300px;
    }
}
#portfolioModal > .modal-container > .modal-main{
    position:relative;
    font-size:24px;
    font-weight:bold;
    color:white;
    padding:10px 0;
}
#portfolioModal > .modal-container > .modal-sub{
    font-size:20px;
    color:white;
    padding:10px 0;

}
#portfolioModal > .modal-container > .modal-text{
    font-size:16px;
    color:#bbb;
}
.text_span{
